.reviews {
  margin-bottom: 230px;
}
@media (max-width: 1600px) {
  .reviews {
    margin-bottom: 190px;
  }
}
.reviews .container {
  max-width: 1390px;
  text-align: center;
}
@media (max-width: 1600px) {
  .reviews .container {
    max-width: 1100px;
  }
}
.reviews h2 {
  margin-bottom: 81px;
}
.reviews .reviews-slider {
  position: relative;
  margin: 0 -30px;
  margin-bottom: 62px;
}
.reviews .slick-track {
  padding-top: 60px;
  padding-bottom: 20px;
}
.reviews .reviews-slide {
  text-align: left;
  margin: 0 30px;
  height: 456px;
  padding: 91px 38px 45px 33px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2);
  color: #333333;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
.reviews .reviews-slide:before {
  z-index: 10;
  content: "";
  width: 146px;
  height: 146px;
  position: absolute;
  top: -63px;
  left: 40px;
  background: url("../img/ava.png");
  background-size: contain;
}
.reviews .review-label {
  font-weight: 500;
  color: #004389;
}
.reviews .slick-arrow {
  font-size: 0;
  position: absolute;
  top: 258px;
  left: -76px;
  background-color: transparent;
  background: url("../img/slider-left-arrow.png") no-repeat;
  width: 58px;
  height: 58px;
  border: none;
  outline: none;
}
.reviews .slick-next {
  background: url("../img/slider-right-arrow.png") no-repeat;
  left: 1466px;
}
@media (max-width: 1600px) {
  .reviews .slick-next {
    left: 1183px;
  }
}
.reviews .look-reviews-btn {
  border: none;
  outline: none;
  background-color: #004389;
  color: #fff;
  position: relative;
  padding: 17px 70px 17px 39px;
}
.reviews .look-reviews-btn:before {
  content: "";
  background: url("../img/video-button-triangle.png") no-repeat;
  width: 22px;
  height: 26px;
  position: absolute;
  top: 16px;
  right: 32px;
}

