  border: none;
  width: 100px;
  height: 66px;
  background: url("../img/footer-up-btn.png") no-repeat;
  background-color: transparent;
  position: relative;
  top: -30px;
}

.modal {
  position: absolute;
  top: 200px;
  left: calc(50% - 276px);
  padding: 64px 86px 70px;
  text-align: center;
  width: 553px;
  height: 690px;
  border-radius: 20px;
  background-color: #fff;
  z-index: 10;
}
.modal .modal__header {
  line-height: 1.6;
  font-size: 25px;
  color: #004389;
  font-weight: bold;
  margin-bottom: 20px;
}
.modal .modal__text {
  line-height: 1.2;
  font-size: 18px;
  color: #333333;
  margin-bottom: 35px;
}
.modal .modal__input {
  width: 100%;
  height: 60px;
  border: none;
  border-bottom: 2px #959595 solid;
  outline: none;
  margin-bottom: 21px;
  padding-left: 70px;
  background: url("../img/form-icons/form-icon1.png") no-repeat top 13px left 21px;
}
.modal .modal__input::placeholder {
  color: #959595;
}
.modal .modal__input:focus {
  background: url("../img/form-icons/form-icon1-focus.png") no-repeat top 13px left 21px;
}
.modal .modal__input:focus::placeholder {
  color: #333333;
}
.modal .phone {
  background-image: url("../img/form-icons/form-icon2.png");
}
.modal .phone:focus {
  background-image: url("../img/form-icons/form-icon2-focus.png");
}
.modal .mail {
  background: url("../img/form-icons/form-icon3.png") no-repeat top 18px left 21px;
}
.modal .mail:focus {
  background: url("../img/form-icons/form-icon3-focus.png") no-repeat top 18px left 21px;
}
.modal .modal__submit-btn {
  margin-top: 20px;
  color: #fff;
  background-color: #004389;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 500;
  height: 60px;
}
.modal .modal-close-btn {
  background-image: url("../img/modal-close-btn.png");
  position: absolute;
  right: 40px;
  top: 40px;
  width: 20px;
  height: 20px;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.3);
  height: 25000px;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
